<template>
    <section>
        <canvas id="animation"></canvas>
        <img id="img" src="../assets/image/Sprite.png" alt="">
    </section>
</template>

<script>
    export default{
        data(){
            return{
                x: 100,
                y: 100,
                image: null
            }
        },
        name: 'SpriteAnimation',
        mounted(){
            //window.addEventListener("load", () => {this.animation()})
            //this.image = new Image(60, 45)
            //this.image.src = "../assets/image/sliderIcon/0.svg"
            this.anim()
        },
        methods:{
            anim(){
                const canvas = document.querySelector('#animation')
                const ctx = canvas.getContext("2d")
                canvas.width = document.querySelector('#animation').clientWidth
                canvas.height = document.querySelector('#animation').clientHeight
                requestAnimationFrame(this.anim)
                //ctx.clearRect(0, 0, innerWidth, innerHeight)
                ctx.beginPath()
                ctx.arc(this.x, this.y, 5, 0, 6.28)
                //ctx.drawImage(this.image, 0, 0, this.width, this.height)
                ctx.fillStyle = 'red'
                ctx.fill()
                if(this.y < 300){
                    this.x += 2
                    this.y += 0.5
                }
            }
            // animation(){
            //     const canvas = document.querySelector('#animation')
            //     const ctx = canvas.getContext("2d")
            //     let img = document.querySelector('#img')
            //     const spriteW = canvas.clientWidth / 2,
            //       spriteH = canvas.clientHeight / 2,
            //       canvasX = 0,
            //       canvasY = 0
     
            //       let cycle = 0;
            //       setInterval(() => {
            //         ctx.clearRect(canvasX, canvasY, spriteW, spriteH)
            //         ctx.drawImage(img, cycle * spriteW, 0, spriteW, spriteH, canvasX, canvasY, spriteW, spriteH)
            //         cycle = (cycle + 1) % 5
            //       }, 100)

            // }
        }
    }
</script>

<style lang="scss" scoped>
    // #animation{
    //     width: 200px;
    //     height: 300px;
    // }
    #animation{
        width: 100%;
        height: 500px;
        background: white;
    }
    #img{
        display: none;
    }
</style>