<template>
    <section>
        <div v-if="error !== null" class="message" ref="message">
            <p>{{ error.message }}</p>
            <button @click="closeMessage">X</button>
        </div>
        <h1>News</h1>
        <div class="graph__buttons">
            <select name="" id="" @click="setCoin()" v-model="coin">
                <option v-for="(item, index) in coinSelect" :key="index" :value="`${item}`" @click="setCoin()">{{ item }}</option>
            </select>
            <button v-for="(item, index) in buttonData" :key="index" id="click" @click="setTime(item)">{{ item }}</button>
        </div> 
        <canvas id="graph" ref="graph"></canvas>
        <sprite-animation />
    </section>
</template>

<script>
    import axios from 'axios'
    import SpriteAnimation from '@/components/SpriteAnimation'

    export default {
        data(){
            return{
                buttonData: ['5min', '15min', '30min', '1h', '4h', '1day', '1week', '1month'],
                newsData: null,
                valueONpx: null,
                coinMin: null,
                coinMax: null,
                width: null,
                height: null,
                coinSelect: ['BTC/USD', 'XRP/USD', 'DOGE/USD', 'LTC/USD', 'ETH/USD'],
                coin: 'BTC/USD',
                newCoin: '',
                time: '1day',
                size: 48,
                error: null
            }
        },
        components:{ SpriteAnimation },
        mounted(){
            this.getData()
        },
        methods:{
            getData(){
                this.height = this.$refs.graph.clientHeight
                this.width = this.$refs.graph.clientWidth
                
                axios
                .get(`https://api.twelvedata.com/time_series?symbol=${this.coin}&type=Stock&interval=${this.time}&outputsize=${this.size}&apikey=d5dc74ee1581433092f67d0f03544937`)
                .then(res => {
                    this.coinMax = 0
                    this.coinMin = 0
                    this.newsData = res.data.values
                    this.error = res.data.status === 'error' ? res.data : null
                    this.newsData.forEach(el => {
                        if(el.high > this.coinMax){
                            this.coinMax = el.high
                            this.coinMin = el.high
                        }
                    })
                    this.newsData.forEach(el => {
                        if(el.low < this.coinMin){
                            this.coinMin = el.low
                        }
                    })
                    this.valueONpx = (this.coinMax - this.coinMin) / (this.height-50) //max - min / height - padding
                    this.graph()
                })
                .catch(() => {
                    this.messageError()
                })
               
            },
            messageError(){
                var c = document.getElementById("graph");
                var ctx = c.getContext("2d");
                c.width = this.width
                c.height = this.height

                ctx.beginPath()
                ctx.font = 'bold 40px Raleway'
                ctx.fillStyle = '#fffff3'
                ctx.textAlign = 'center'
                ctx.fillText(this.error.code, this.width, this.height / 2)   
                ctx.stroke()
                ctx.closePath()             
            },
            closeMessage(){
                this.$refs.message.animate([
                    {transform: 'translateX(0)'},
                    {transform: 'translateX(105%)'}
                ],{
                    duration: 500,
                    fill: 'both'
                })
                setTimeout(() => this.error = null, 500)
            },
            graph(){
                var c = document.getElementById("graph");
                var ctx = c.getContext("2d");
                c.width = this.width
                c.height = this.height
                
                ctx.beginPath()
                ctx.lineWidth = 1 
                ctx.strokeStyle = '#fffff3'
                ctx.font = 'bold 20px Raleway'
                ctx.fillStyle = '#fffff3'
                for(let i = 1; i<=5; i++){
                    let y = (this.height - 50) / 5 * i, value = y*this.valueONpx + parseInt(this.coinMin)
                    let values = value > 100 ? value.toFixed(1) : value < 100 && value > 10 ? value.toFixed(2) : value < 10 && value > 1 ? value.toFixed(3) : value.toFixed(4)
                    ctx.fillText(values, 5, this.height - y - 5)
                    ctx.moveTo(0, this.height - y)
                    ctx.lineTo(this.width, this.height - y)
                }
                ctx.stroke()
                ctx.closePath()
              
                ctx.beginPath()
                ctx.lineWidth = 3 
                ctx.strokeStyle = '#63AF8F'
                for(let index in this.newsData){
                    let X = this.width / this.newsData.length * index, Y = this.height - (this.newsData[this.newsData.length - index - 1].close - this.coinMin) / this.valueONpx
                    ctx.lineTo(X, Y)
                }
                ctx.stroke()
                ctx.closePath()

                ctx.beginPath()
                ctx.lineWidth = 2 
                ctx.strokeStyle = '#5F3791'
                ctx.fillStyle = '#5F3791'
                for(let index in this.newsData){
                    let X = this.width / this.newsData.length * index, Y = this.height - (this.newsData[this.newsData.length - index - 1].close - this.coinMin) / this.valueONpx,
                        date = this.newsData[this.newsData.length - index - 1].datetime.length > 10 ? this.newsData[this.newsData.length - index - 1].datetime.substring(this.newsData[this.newsData.length - index - 1].datetime.length - 8) : this.newsData[this.newsData.length - index - 1].datetime
                    if(index % (this.newsData.length / 6) === 0){
                        ctx.moveTo(X, this.height - 20)
                        ctx.lineTo(X, Y)
                        ctx.fillText(date, X, this.height - 5)
                    }
                }
                ctx.stroke()
                ctx.closePath()

            },
            setCoin(){
               this.newCoin !== this.coin && this.newCoin !== '' ? this.getData() : false
               this.newCoin = this.coin
            },
            setTime(time){
                if(time === '5min'){
                    this.time = '5min'
                    this.size = 48
                }else if(time === '15min'){
                    this.time = '15min'
                    this.size = 48
                }else if(time === '30min'){
                    this.time = '30min'
                    this.size = 48
                }else if(time === '1h'){
                    this.time = '1h'
                    this.size = 48
                }else if(time === '4h'){
                    this.time = '4h'
                    this.size = 48
                }else if(time === '1day'){
                    this.time = '1day'
                    this.size = 48
                }else if(time === '1week'){
                    this.time = '1week'
                    this.size = 48
                }else if(time === '1month'){
                    this.time = '1month'
                    this.size = 48
                }
                this.getData()
            }
        }
    }
</script>

<style lang="scss" scoped>
    #graph{
        background: #000100;
        width: 100%;
        height: 500px;
    }
    .message{
        position: absolute;
        bottom: 15px;
        right: 0;
        background: red;
        border-radius: 15px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        p{
            opacity: 1;
            color: white;
            width: calc(100% - 110px);
            padding: 20px 0;
            font-size: 18px;
            font-weight: 500;
            max-width: 955px;
        }
        button{
            background: white;
            border-radius: 50%;
            width: 40px;
            height: 40px;
            margin-right: 10px;
            color: #ff0000;
            border: 2px solid #940505;
            &:hover{
                transition: .5s;
                cursor: pointer;
                box-shadow: 0 0 5px #940505;
                color: #940505
            }
        }
    }
    button, select{
        width: 70px;
        height: 50px;
        background: #63AF8F;
        color: #fffff0;
        font-size: 16px;
        font-weight: 600;
        border-radius: 5px;
        &:hover{
            transition: .5s;
            cursor: pointer;
            border: 3px solid #5F3791;
            box-shadow: 0 0 5px #5F3791;
            color: #5F3791
        }
    }
    select{
        width: 200px;
        height: 50px;
    }

    .graph__buttons{
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        width: 100%;
        padding: 25px 0;
    }
</style>

